export default function LinkTransformer({ link }: { link: string }) {
  if (link.includes('http')) {
    return (
      <span>
        <a
          href={link}
          target="_blank"
          className="underline"
          rel="noopener noreferrer"
        >
          {link}
        </a>
      </span>
    );
  }
  return <span>{link}</span>;
}
